*{margin: 0; padding: 0;}
@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap');
body{
    font-family: 'Gowun Dodum', sans-serif;
}
*{margin: 0; padding: 0;}


#body>h1{margin-top: 16px; margin-bottom: 10px;}
#banner{height: 300px; background: #ff0;}
#banner>img{width: 100%;}
#product-list{display: flex; flex-wrap: wrap;}
.product-card{width: 180px; /* height: 310px; */ border: 1px solid rgba(230,230,230,1); margin-right: 12px; margin-bottom: 12px;}
.product-link{color: #333; text-decoration: none;}
.product-card .product-img{width: 100%; height: 210px;}
.product-card .product-contents{padding: 8px; display: flex; flex-direction: column;}
.product-card .product-contents .product-name{font-size: 14px; color: rgb(88, 88, 88);}
.product-card .product-contents .product-price{margin-top: 5px; font-weight: 600;}
.product-card .product-contents .product-footer{display: flex; justify-content: space-between; align-items: center; margin-top: 10px;}
.product-card .product-contents .product-seller{display: flex; align-items: center; margin-top: 12px; margin-bottom: 10px;}
.product-card .product-contents .product-seller .product-avatar{width: 24px; margin-right: 5px;}
.product-card .product-contents .product-seller span{font-size: 14px; color: #666;}
.product-card .product-contents .product-date{font-size: 12px; color: #8a8a8a;}
